body {
  font-family: 'PT Sans', sans-serif;
  /* padding: 2rem; */
}

button {
  border: none;
  border-radius: 0.25rem;
  font-size: 0.75rem;
  font-weight: bold;
  padding: 0.75em;
  display: inline-block;
  text-decoration: none;
}
button :hover {
cursor: pointer;
}
button :focus {
text-decoration: none;
outline: none;
}

.comment-box {
  max-width: 37.5rem;
  color: #fff;
  background-color: #302f2f;
  border-radius: 0.875rem;
  padding: 0.2rem 1rem 2rem;
  margin-left: 1rem;
  margin-bottom: 1rem;
}
.comment-form {
  background-color: #fff;
  border-radius: 0.25rem;
  margin-bottom: 2rem;
  padding: 1rem;  
}
.comment-form-fields {
  margin-bottom: 0.25rem;
}
input, textarea {
border: none;
border-bottom: 1px solid #ffffff;
font-size: 0.85rem;
padding: 0.25rem 0;
width: 99%;
}
    
.comment-form-fields, input, textarea :focus {
    border-bottom-color: #00BDFC;
    outline: none;
}

textarea {
    font-style: italic;
}

.comment-count {
  color: #dad8d8;
}
#comment-reveal {
  float: right;
  background-color: #383838;
  color: #fff;
}
.comment {
  border-top: 0.125rem solid #7e7d7d;
}
.comment-body {
  font-style: italic;
  margin-left: 1rem;
}
.comment-footer {
  margin-bottom: 1rem;
}
.comment-footer-delete {
  padding: 0.2rem 0;
  color: #a8a8a8;
  text-decoration: none;
}
.comment-footer-delete :hover {
    color: #00BDFC;
    font-weight: bold;
}

